import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import moment from 'moment';
import React, { Component } from 'react';
import { connect } from 'react-redux';
import { onNext, onPrev, setDate, setDay } from '../../store/actions/appointmentActions';
import './RPCalendar.css';

class RPCalendar extends Component {
  state = {
    dateContext: moment(),
    today: moment(),
  };

  weekdaysShort = moment.weekdaysShort();
  months = moment.months();

  year = () => this.state.dateContext.format('Y');
  currentYear = () => this.state.today.format('Y');
  month = () => this.state.dateContext.format('MMMM');
  currentMonth = () => this.state.today.format('MMMM');
  daysInMonth = () => this.state.dateContext.daysInMonth();
  currentDate = () => this.state.dateContext.get('date');
  currentDay = () => this.state.dateContext.format('DD');
  firstDayOfMonth = () =>
    moment(this.state.dateContext)
      .startOf('month')
      .format('d');

  onPrev = () => {
    this.setState(
      {
        dateContext: this.state.dateContext.subtract(1, 'month')
      },
      () => this.props.onPrev()
    );
  };

  onNext = () => {
    this.setState(
      {
        dateContext: this.state.dateContext.add(1, 'month')
      },
      () => this.props.onNext()
    );
  };

  onDayClick = (e, d) => {
    let selectedDay = d < 10 ? '0' + d : d;
    let selectedDate = `${this.month()} ${selectedDay}, ${this.year()}`;
    this.props.setDay(selectedDay);
    this.props.setDate(selectedDate);
  };

  render() {
    let weekdaysShortName = this.weekdaysShort.map(day => (
      <th key={day} className="px-3 py-3 week-day">
        {day}
      </th>
    ));

    let blanks = [];
    for (let i = 0; i < this.firstDayOfMonth(); i++) {
      blanks.push(
        <td key={i * 9} className="calendar-day empty">
          {''}
        </td>
      );
    }

    let daysInMonth = [];
    for (let d = 1; d <= this.daysInMonth(); d++) {
      let calendarDayClass =
        d === +this.currentDay() &&
        this.month() === this.currentMonth() &&
        this.year() === this.currentYear()
          ? ' calendar-day today'
          : ' calendar-day';
      let selectedDayClass = d === +this.props.selectedDay ? ' selected-day' : '';

      daysInMonth.push(
        <td
          key={d}
          className={"border border-primary text-center" + calendarDayClass + selectedDayClass}
          onClick={e => {
            d < +this.currentDay() &&
            this.month() === this.currentMonth() &&
            this.year() === this.currentYear()
              ? e.preventDefault()
              : this.onDayClick(e, d);
          }}>
          <span>{d}</span>
        </td>
      );
    }

    var totalSlots = [...blanks, ...daysInMonth];
    let rows = [];
    let cells = [];

    totalSlots.forEach((row, i) => {
      if (i % 7 !== 0) {
        cells.push(row);
      } else {
        rows.push(cells);
        cells = [];
        cells.push(row);
      }
      if (i === totalSlots.length - 1) {
        rows.push(cells);
      }
    });

    let monthDays = rows.map((d, i) => {
      return <tr key={i}>{d}</tr>;
    });

    return (
      <div className="border border-primary">
        <div className="border border-primary">
        <span className="float-left"
          onClick={e => {
            Number(
              moment()
                .month(this.month())
                .format('M')
            ) >
              Number(
                moment()
                  .month(this.currentMonth())
                  .format('M')
              ) && Number(this.year() >= this.currentYear())
              ? this.onPrev()
              : e.preventDefault();
          }}>
          <FontAwesomeIcon icon="chevron-left" />
        </span>
        <span>
          {this.month()} {this.year()}
        </span>
        <span className="float-right"
          onClick={e => {
            this.onNext();
          }}>
          <FontAwesomeIcon icon="chevron-right" />
        </span></div>
        <table>
          <thead>
            <tr className="border border-primary">{weekdaysShortName}</tr>
          </thead>
          <tbody className="border border-primary">{monthDays}</tbody>
        </table>
      </div>
    );
  }
}

const mapStateToProps = state => {
  return {
    selectedDay: state.appointment.selectedDay
  };
};

const mapDispatchToProps = dispatch => {
  return {
    setDate: date => dispatch(setDate(date)),
    setDay: day => dispatch(setDay(day)),
    onNext: () => dispatch(onNext()),
    onPrev: () => dispatch(onPrev())
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(RPCalendar);
