import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import React, { Component, Fragment } from 'react';
import { Dropdown } from 'react-bootstrap';
import { connect } from 'react-redux';
import { Link, NavLink } from 'react-router-dom';
import logo from '../../../assets/Physio-logo.png';
import { logout } from '../../../store/actions/authActions';
import './header.css';

class Header extends Component {
  state = {
    showCollapsedMenu: false
  };

  toggleMenu = () => {
    this.setState({
      showCollapsedMenu: !this.state.showCollapsedMenu
    });
  };
  render() {
    const { auth, profile } = this.props;
    const show = this.state.showCollapsedMenu ? 'show' : '';

    const links = auth.uid ? (
      <Fragment>
        <Dropdown>
          <Dropdown.Toggle>{profile.initials}</Dropdown.Toggle>
          <Dropdown.Menu>
            <Dropdown.Item onClick={this.props.logout}>Logout</Dropdown.Item>
          </Dropdown.Menu>
        </Dropdown>
      </Fragment>
    ) : (
      <Fragment>
        <NavLink to="/login" className="nav-link ml-0 text-white">
          <FontAwesomeIcon
            icon="user-circle"
            className="nav-icon float-left mr-2 mt-1"
            color="white"
            size="2x"
          />
          Login
        </NavLink>
      </Fragment>
    );
    return (
      <nav className="navbar navbar-expand-lg navbar-dark bg-dark py-3">
        <div className="container">
          <NavLink to="/" className="navbar-brand">
            <img src={logo} alt="" className="img-fluid" style={{ width: 220 + 'px' }} />
          </NavLink>
          <button
            className="navbar-toggler"
            type="button"
            onClick={this.toggleMenu}
            data-toggle="collapse"
            data-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent"
            aria-expanded="false"
            aria-label="Toggle navigation">
            <span className="navbar-toggler-icon"></span>
          </button>
          <div className={'collapse navbar-collapse ' + show} id="navbarSupportedContent">
            <ul className="navbar-nav mr-auto">
              <li className="nav-item">
                <NavLink to="/" exact className="nav-link">
                  Home
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink to="/contact" className="nav-link">
                  Contact
                </NavLink>
              </li>
              <li className="nav-item">
                <NavLink to="/faqs" className="nav-link">
                  FAQs
                </NavLink>
              </li>
            </ul>
            <form className="form-inline my-2 my-lg-0">
              {links}
              <Link to="/therapist" type="button" className="btn btn-light px-4 shadow-sm">
                Book A Visit
              </Link>
            </form>
          </div>
        </div>
      </nav>
    );
  }
}

const mapStateToProps = state => {
  // console.log(state)
  return {
    auth: state.firebase.auth,
    profile: state.firebase.profile
  };
};

const mapDispatchToProps = dispatch => {
  return {
    logout: () => dispatch(logout())
  };
};

export default connect(mapStateToProps, mapDispatchToProps)(Header);
